.docimg {
    position: absolute;
    top: 50px; /* Adjust as needed */
    right: 50px; /* Adjust as needed */
    max-height: 350px; /* Adjust as needed */
    width: auto;
    
  }
  .main-text{
    color: rgb(3, 22, 44);
   /*font-size: 100px;  Adjust font size as needed */
    position: absolute;
    left: 20px; /* Adjust as needed */
    top: 30%;
    font-family: 'Bebas Neue';
    transform: translateY(-50%);
    font-weight: bold;
    font-size: 80px;
    background-image: linear-gradient(gold, gold);
    background-size: 100% 10px;
    background-repeat: no-repeat;
    background-position: 100% 0%;
    transition: background-size .7s, background-position .5s ease-in-out;
     
  }
  .main-text:hover {
    background-size: 100% 100%;
    background-position: 0% 100%;
    transition: background-position .7s, background-size .5s ease-in-out;
  }

    .content {
        position: relative;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center; /* Center main content horizontally */
        background-image: url('../background.webp'); /* Adjust path as necessary */
        background-size: cover; /* Ensure the image covers the whole area */
        background-position: center; /* Center the image */
      }

      .explore-button {
        display: inline-block;
         margin-top: 20px;
        padding: 10px 20px; 
        font-size: 16px;
        color: white;
        background-color: #007bff; /* Bootstrap primary color */
        border: none;
        border-radius: 4px;
        text-decoration: none;
        cursor: pointer;
        position: absolute;
        left: 30px; /* Adjust as needed */
        top: 45%;
        transform: translateY(-50%);
        font-weight: bold;
      }
      
      .explore-button:hover {
        background-color: #0056b3; /* Darker shade of the primary color */
      }


      .description-text {
        position: absolute;
        bottom: 350px;
        left: 20px;
        font-weight: bold;
        font-size: 20px;
        color: #080707;
        font-family:Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
        margin: 0; /* Remove default margin */
      }



      @media (max-width: 768px) {
        .docimg {
          top: 20px;
          right: 20px;
          max-height: 200px; /* Adjust as needed */
        }
      
        .main-text {
          left: 10px; /* Adjust as needed */
          top: 20%;
          font-size: 40px; /* Adjust font size for mobile */
        }
      
        .explore-button {
          left: 10px; /* Adjust as needed */
          top: 35%;
          font-size: 14px;
          padding: 8px 16px;
        }
      
        .description-text {
          bottom: 150px;
          left: 10px;
          font-size: 16px;
        }
      
      }
      
      @media (max-width: 450px) {
        .docimg {
          top: 10px;
          right: 10px;
          max-height: 150px; /* Adjust as needed */
        }
      
        .main-text {
          left: 5px; /* Adjust as needed */
          top: 15%;
          font-size: 30px; /* Adjust font size for mobile */
        }
      
        .explore-button {
          left: 5px; /* Adjust as needed */
          top: 30%;
          font-size: 12px;
          padding: 6px 12px;
        }
      
        .description-text {
          bottom: 100px;
          left: 5px;
          font-size: 14px;
        }
      
       
      }

     